import { Injectable, OnInit } from '@angular/core';
import { Observable, isObservable } from 'rxjs';

@Injectable()
export abstract class PortalSearchPage<T, C> implements OnInit {
  searchInProgress: boolean;
  public results: T[];

  public criteria: C;
  abstract doSearch(): T[] | Observable<T[]>;
  abstract getDefaultCriteria(): C;

  search() {
    this.searchInProgress = true;
    const resultIntermediate = this.doSearch();
    if (isObservable(resultIntermediate)) {
      resultIntermediate.subscribe(data => {
        this.results = data;
        this.searchInProgress = false;
      });
    } else {
      this.results = resultIntermediate;
      this.searchInProgress = false;
    }
  }

  reset() {
    this.results = [];
    this.criteria = this.getDefaultCriteria();
  }

  ngOnInit() {
    this.reset();
  }
}
