import { Injectable } from '@angular/core';

@Injectable({
  providedIn: "root"
})
export class StateService {
  private userSettingsActivatedTab: number;

  public getUserSettingsActivatedTab(): number {
    return this.userSettingsActivatedTab;
  }
  public setUserSettingsActivatedTab(tab: number): void {
    this.userSettingsActivatedTab = tab;
  }
}
