import { HttpClient } from '@angular/common/http'
import { Injectable, Inject } from '@angular/core'
import { APP_CONFIG, LibConfig } from './injection-tokens'
import { PortalApiService } from './services/portal-api.service'
import { Portal } from './model/portal'
import { ReplaySubject } from 'rxjs'

const TKIT_PORTAL_ID = 'TKIT_PORTAL_ID'

@Injectable({
  providedIn: 'root',
})
export class ConfigurationService {
  public lang$ = new ReplaySubject<string>(1)

  private customEnv: any
  private portalData: Portal
  error: any

  constructor(
    private http: HttpClient,
    private portalService: PortalApiService,
    @Inject(APP_CONFIG) private appConfig: LibConfig
  ) {}

  public init(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      if (this.appConfig.skipRemoteConfigLoad) {
        console.log(
          'TKA001: Remote config load is disabled. To enable it, remove the "skipRemoteConfigLoad" key in your environemnt.json'
        )
        this.customEnv = this.appConfig
        resolve(false)
      }
      let loadConfigPromise: Promise<any>
      // if we have Backend env vars injected in HTML, use it
      console.log(`ENV resolved ${window['APP_CONFIG']}`)
      if (window['APP_CONFIG']) {
        loadConfigPromise = new Promise((resolve, reject) => {
          resolve(window['APP_CONFIG'])
        })
      } else {
        console.log('noooo moar http')
        //otherwise fetch them from env.json
        loadConfigPromise = this.http.get(this.appConfig.remoteConfigURL || 'assets/env.json').toPromise()
      }

      loadConfigPromise
        .then(config => {
          if (config) {
            const sanitizedEnvValues = this.sanitizeConfig(config)
            this.customEnv = { ...this.appConfig, ...sanitizedEnvValues }
          }
        })
        .then(() => {
          const portalId = this.customEnv[TKIT_PORTAL_ID] || 'PORTAL'

          this.portalService
            .getPortalData(portalId)
            .toPromise()
            .then(data => {
              if (data) {
                this.portalData = data
              }
              resolve(true)
            })
            .catch(err => {
              console.log(`Failed to load portal data! ${JSON.stringify(err)}`)
              this.portalData = { portalId: 'PORTAL' }
              resolve(true)
            })
        })
        .catch(e => {
          console.log(`Failed to load env configuration`)
          reject(e)
        })
    })
  }

  private sanitizeConfig(config: Object) {
    return (
      Object.keys(config)
        // only take values that are interpolated/set
        .filter(key => !(config[key] as string).startsWith('${'))
        .reduce((obj, key) => {
          obj[key] = config[key]
          return obj
        }, {})
    )
  }

  public getProperty(key: string): string {
    return this.customEnv[key]
  }

  public getPortalData(): Portal {
    return this.portalData
  }
  public setLang(lang: string) {
    console.log('[CONFIG SERVICE] SETTING LANG: ', lang)
    this.lang$.next(lang)
  }
}
