import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  ValidatorFn,
  Validators
} from '@angular/forms';
import { FeedbackBasic } from '../../../api/model/feedback.model';

@Component({
  selector: 'tk-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class FeedbackComponent implements OnInit {
  form: FormGroup;
  minLength = 5;

  @Input()
  submitDisabled = false;
  @Input()
  set feedbackData(feedback: FeedbackBasic) {
    if (this.form) {
      this.form.patchValue(feedback);
    }
  }

  @Output()
  feedbackDataChange: EventEmitter<FeedbackBasic> = new EventEmitter();
  @Output()
  feedbackSubmit: EventEmitter<FeedbackBasic> = new EventEmitter();
  commentRequired = false;

  requiredCommentValidator: ValidatorFn = (fg: FormGroup) => {
    const rating = fg.controls.rating.value;
    const comment = fg.controls.comment.value;
    if (rating > 0 && rating < 5) {
      this.commentRequired = true;
      return comment.length >= this.minLength ? null : { missingComment: true };
    } else {
      this.commentRequired = false;
      return null;
    }
  };

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    this.form = this.fb.group({
      rating: [0, [Validators.required, Validators.min(1), Validators.max(5)]],
      comment: ['', Validators.minLength(this.minLength)],
      anonymous: false
    }, { validators: this.requiredCommentValidator });
    this.form.valueChanges.subscribe(values => {
      this.feedbackDataChange.emit(values);
    });
  }

  submitFeedback() {
    if (this.form.valid && !this.submitDisabled) {
      this.submitDisabled = true;
      this.feedbackSubmit.emit(this.form.value);
    }
  }
}
