import { Directive, Input, Renderer2, ElementRef, Inject, ViewContainerRef, TemplateRef, OnInit } from '@angular/core';
import { IAuthService } from '../../api/iauth.service';
import { AUTH_SERVICE } from '../../api/injection-tokens';

@Directive({ selector: '[tkIfPerm]' })
export class TkIfPermDirective implements OnInit {
  permission: string;
  @Input() loaderState: boolean;
  constructor(
    private templateRef: TemplateRef<any>,
    private viewContainer: ViewContainerRef,
    @Inject(AUTH_SERVICE) private authService: IAuthService
  ) {}

  @Input()
  set tkIfPerm(permission: string) {
    this.permission = permission;
  }

  ngOnInit() {
    if (!this.authService.hasPermission(this.permission)) {
      console.log(`Hide element because no perm ${this.permission}`);
      this.viewContainer.clear();
    } else {
      this.viewContainer.createEmbeddedView(this.templateRef);
    }
  }
}
