import { Component, OnInit, Input } from '@angular/core';
// TODO is <a> without click actually OK? How about a11y?
@Component({
  selector: 'tk-toolbar-item',
  template: `
    <a
      pRipple
      *ngIf="link"
      class="toolbar-item"
      [href]="link"
      [target]="linkTarget"
      [rel]="linkRel"
      title="{{ title }}"
      role="button"
      ><i class="pi pi-{{icon}}"></i>
    </a>
    <a
      pRipple
      *ngIf="!link"
      href="javascript:void(0)"
      class="toolbar-item"
      title="{{ title }}"
      role="button"
      ><i class="pi pi-{{icon}}"></i>
    </a>
  `,
  styles: [
    `
      .toolbar-item {
        color: #757575;
        display: inline-block;
        padding: 3px;
        text-align: center;
        font-size: 1.3em;
        transition: all 0.4s;
      }
      .toolbar-item i {
        transition: all 0.4s;
        font-size: 1.5rem;
        padding: 0.2em;
      }
      .toolbar-item:hover {
        background-color: #e8e8e8;
      }
      .toolbar-item:focus {
        background-color: #e8e8e8;
      }

    `
  ]
})
export class ToolbarItemComponent implements OnInit {
  @Input()
  title: string;

  @Input()
  permission: string;

  @Input() link: string;
  @Input() linkTarget: string;
  @Input() linkRel: string;
  @Input()
  action: Function;

  @Input()
  icon: string;

  constructor() {}

  ngOnInit() {}
}
