import { Component, Input } from "@angular/core";
import { Portal } from "../../api/model/portal";

@Component({
  selector: "tk-footer",
  styles: [
    `
      .footer span {
        vertical-align: middle;
      }
      .footer-right-segment a {
        margin-left: 5px;
        margin-right: 5px;
      }
      .footer-right-segment {
        margin-right: 10px;
        float: right;
      }
      .footer-left-segment {
        margin-left: 15px;
        float: left;
      }
      .footer-left-segment a {
        color: #000000;
      }
    `
  ],
  template: `
    <div 
      class="footer"
      id="footer"
      role="contentinfo"
    >
      <span class="footer-left-segment">
        <a
          *ngIf="homePortal.homePage; else withoutHref"
          [href]="homePortal.homePage"
        >
          {{ homePortal.footerLabel }}
        </a>
        <ng-template #withoutHref>
          <a>
            {{ homePortal.footerLabel }}
          </a>
        </ng-template>
        -
        <span>&copy; All Rights Reserved</span>
      </span>
      <div class="footer-right-segment">
        <span class="links" *ngFor="let link of homePortal.subjectLinks">
          <a [href]="link.url">{{ link.label }}</a>
        </span>
      </div>
    </div>
  `
})
export class AppFooterComponent {
  @Input() homePortal: Portal;
}
