import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { SearchTemplate } from '../model/search-template.model';
import { Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class SearchTemplateAPIService {
  private readonly baseUrl: string = './portal-api/v1/searchTemplates';

  private readonly httpOptions: { [key: string]: any } = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      Accept: 'application/json'
    })
  };

  constructor(private httpClient: HttpClient) {}

  getSearchTemplatesLegacy(
    user: string,
    application: string,
    page: string,
    includeGlobal = true
  ): Observable<SearchTemplate[]> {
    return this.httpClient.post<SearchTemplate[]>(
      `${this.baseUrl}/search/criteria`,
      {
        application,
        page,
        user,
        includeGlobal
      },
      this.httpOptions
    );
  }

  getTemplateById(id: string): Observable<SearchTemplate> {
    return this.httpClient.get<SearchTemplate>(
      `${this.baseUrl}/${id}`,
      this.httpOptions
    );
  }

  createNewTemplate(template: SearchTemplate): Observable<SearchTemplate> {
    return this.httpClient.post<SearchTemplate>(
      `${this.baseUrl}`,
      template,
      this.httpOptions
    );
  }

  updateTemplate(
    id: string,
    template: SearchTemplate
  ): Observable<SearchTemplate> {
    return this.httpClient.put<SearchTemplate>(
      `${this.baseUrl}/${id}`,
      template,
      this.httpOptions
    );
  }

  deleteTemplate(id: string): Observable<boolean> {
    return this.httpClient.delete<boolean>(`${this.baseUrl}/${id}`);
  }
}
