import { HttpInterceptor, HttpHandler, HttpRequest, HttpEvent, HttpResponse } from '@angular/common/http'
import { Observable } from 'rxjs'
import { tap } from 'rxjs/operators'
import { HttpProgressLoaderService } from './http-progress-loader.service'
import { Injectable } from '@angular/core'

@Injectable({ providedIn: 'root' })
export class HttpProgressInterceptor implements HttpInterceptor {
  private requests: HttpRequest<any>[] = []
  constructor(private loaderService: HttpProgressLoaderService) {}

  removeRequest(req: HttpRequest<any>) {
    const i = this.requests.indexOf(req)
    if (i >= 0) {
      this.requests.splice(i, 1)
    }
    this.loaderService.isLoading.next(this.requests.length > 0)
  }
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const started = Date.now()
    this.requests.push(req)
    this.loaderService.isLoading.next(true)
    return next.handle(req).pipe(
      tap(
        event => {
          if (event instanceof HttpResponse) {
            const elapsed = Date.now() - started
            console.log(
              `Request for ${req.urlWithParams} took ${elapsed} ms with result ${event.status}. ${this.requests.length}`
            )
            this.removeRequest(req)
          }
        },
        error => {
          console.log(`Http err: status ${error.status} ${JSON.stringify(error)}`)
          this.removeRequest(req)
        }
      )
    )
  }
}
