export function HttpLoaderFactory2(http: HttpClient) {
  return new TranslateHttpLoader(http, './portal-lib/assets/i18n/')
}

export function StandardTranslateHttpLoader(http: HttpClient) {
  return new MultiTranslateHttpLoader(http, [
    { prefix: './assets/i18n/', suffix: '.json' },
    { prefix: './portal-lib/assets/i18n/', suffix: '.json' },
  ])
}

