import {
  Component,
  OnInit,
  Input,
  TemplateRef,
  ViewChild,
  ElementRef,
  ContentChild,
  Inject,
  Injector
} from '@angular/core';
import { CollapsibleDirective } from '../collapsible.directive';
import { IAuthService } from '../../../api/iauth.service';
import { AUTH_SERVICE } from '../../../api/injection-tokens';

@Component({
  selector: 'tk-portal-page',
  templateUrl: './portal-page.component.html',
  styleUrls: ['./portal-page.component.css']
})
export class PortalPageComponent implements OnInit {
  @Input() header: string;
  @Input() permission: string;
  @Input() helpArticleId: string;
  @Input() leftToolbar: TemplateRef<any>;
  @Input() rightToolbar: TemplateRef<any>;

  @ContentChild(CollapsibleDirective, { read: ElementRef })
  collapsible2: ElementRef;

  collapsed = false;

  authService: IAuthService;

  constructor(private injectorObj: Injector) {
    this.authService = this.injectorObj.get(AUTH_SERVICE);
  }

  ngOnInit() {}

  doSomething() {
    alert('Hey it worked!');
  }

  hasAccess() {
    return this.permission
      ? this.authService.hasPermission(this.permission)
      : true;
  }

  collapse() {
    this.collapsible2.nativeElement.classList.add('hidden');
    this.collapsed = true;
  }
  expand() {
    this.collapsible2.nativeElement.classList.remove('hidden');
    this.collapsed = false;
  }
}
