import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core'
import { IconsService } from '../../../api/services/icons.service'
import { FormBuilder, FormGroup, Validators } from '@angular/forms'
import { GetAllLaunchpadsFilteredRequestParams, LaunchpadService } from '../../../api/services/launchpad.service'
import { AUTH_SERVICE } from '../../../api/injection-tokens'
import { IAuthService } from '../../../api/iauth.service'
import { LaunchpadDTO } from '../../../api/model/launchpadDTO'
import { LaunchpadTileCreateDTO } from '../../../api/model/launchpadTileCreateDTO'
import { TileType } from '../../../api/model/tileType'
import { MessageService } from 'primeng/api'
import { ConfigurationService } from '../../../api/configuration.service'

@Component({
  selector: 'tk-add-to-launchpad',
  templateUrl: './add-to-launchpad.component.html',
  styleUrls: ['./add-to-launchpad.component.scss'],
})
export class AddToLaunchpadComponent implements OnInit {
  @Input()
  public display: boolean
  @Input()
  public appId: string
  @Output()
  public closed = new EventEmitter<boolean>()

  public launchpads: LaunchpadDTO[] = []
  public icons: string[]
  public filteredIcons: string[]
  public launchpadForm: FormGroup

  constructor(
    private iconsService: IconsService,
    private fb: FormBuilder,
    private launchpadService: LaunchpadService,
    @Inject(AUTH_SERVICE) private readonly auth: IAuthService,
    private readonly messageService: MessageService,
    private readonly confService: ConfigurationService
  ) {}

  public ngOnInit(): void {
    this.iconsService.getPrimeicons().subscribe(
      icons => (this.icons = icons.filter(i => !!i)),
      error => console.log(error)
    )
    this.launchpadForm = this.fb.group({
      icon: ['', Validators.required],
      type: [{ value: 'FAVOURITE', disabled: true }],
      name: ['', Validators.required],
      appId: [{ value: this.appId, disabled: true }],
      appUrl: [
        {
          value: window.location.href.replace(window.location.origin, ''),
          disabled: true,
        },
      ],
      launchpad: ['', Validators.required],
    })

    const launchpadCriteria: GetAllLaunchpadsFilteredRequestParams = {
      portalId: this.confService.getProperty('TKIT_PORTAL_ID'),
    }
    this.launchpadService.getLaunchpads(launchpadCriteria).subscribe(
      data => (this.launchpads = data),
      error => console.log(error)
    )
  }

  public addToLaunchpad(): void {
    const createTileDTO: LaunchpadTileCreateDTO = {
      appId: this.launchpadForm.get('appId').value as string,
      appUrl: this.launchpadForm.get('appUrl').value as string,
      name: this.launchpadForm.get('name').value as string,
      type: this.launchpadForm.get('type').value as TileType,
      icon: this.launchpadForm.get('icon').value as string,
    }
    this.launchpadService.addToLaunchpad(this.launchpadForm.get('launchpad').value.id, createTileDTO).subscribe(
      () => {
        this.messageService.add({
          summary: 'App added to favourites successfully',
          severity: 'success',
        })
        this.close()
      },
      () => {
        this.messageService.add({
          summary: 'Failed to add app to favourites',
          severity: 'error',
        })
      }
    )
  }

  public filterIcons(event): void {
    const query = event.query.toLowerCase()
    this.filteredIcons = this.icons.filter(icon => icon.toLowerCase().includes(query))
  }

  public close(event?: any) {
    this.closed.emit(false)
  }
}
