import { Component, HostBinding, Input, OnDestroy, OnInit, Output } from '@angular/core'
import { Subscription } from 'rxjs'
import { BreadcrumbService } from '../../../api/services/breadcrumb.service'
import { EventEmitter } from '@angular/core'
import { MenuItem } from 'primeng/api'

@Component({
  selector: 'tk-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.scss'],
})
export class BreadcrumbComponent implements OnInit, OnDestroy {
  subscription: Subscription

  items: any[]

  home: MenuItem

  @Input()
  disableBreadcrumbs: boolean
  @Input()
  fullPortalLayout: boolean
  @Output()
  openHelpPage: EventEmitter<any> = new EventEmitter()
  @Output()
  openHelpPageEditor: EventEmitter<any> = new EventEmitter()

  constructor(private breadcrumbService: BreadcrumbService) {}

  ngOnInit() {
    this.subscription = this.breadcrumbService.itemsHandler.subscribe(response => {
      this.items = response
    })

    this.home = { icon: 'pi pi-home', routerLink: '/' }
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe()
    }
  }

  onOpenHelpPageEditor(evt: any) {
    this.openHelpPageEditor.emit(evt)
  }

  onOpenHelpPage(evt: any) {
    this.openHelpPage.emit(evt)
  }
}
