import {Component, EventEmitter, Inject, Input, OnInit, Output} from '@angular/core';
import {
  animate,
  state,
  style,
  transition,
  trigger
} from '@angular/animations';
import { AUTH_SERVICE } from '../../api/injection-tokens';
import { IAuthService } from '../../api/iauth.service';
import { UserProfile } from '../../api/model/user-profile.model';
import { DEFAULT_AVATAR_URL } from '../constants';
import { AvatarInfo } from '../../api/model/avatar-info.model';
import { Router } from '@angular/router';
import { ConfigurationService } from '../../api/configuration.service';

@Component({
  selector: 'tk-inline-profile',
  templateUrl: 'inline-profile.component.html',
  styleUrls: ['./inline-profile.component.scss'],
  animations: [
    trigger('menu', [
      state('hiddenAnimated', style({
        height: '0px',
        paddingBottom: '0px',
        overflow: 'hidden'
      })),
      state('visibleAnimated', style({
        height: '*',
        overflow: 'visible'
      })),
      state('visible', style({
        opacity: 1,
        'z-index': 100
      })),
      state('hidden', style({
        opacity: 0,
        'z-index': '*'
      })),
      transition('visibleAnimated => hiddenAnimated', animate('400ms cubic-bezier(0.86, 0, 0.07, 1)')),
      transition('hiddenAnimated => visibleAnimated', animate('400ms cubic-bezier(0.86, 0, 0.07, 1)')),
      transition('visible => hidden', animate('.1s linear')),
      transition('hidden => visible', [style({transform: 'scaleY(0.8)'}), animate('.12s cubic-bezier(0, 0, 0.2, 1)')])
    ])
  ]
})
export class AppInlineProfileComponent implements OnInit {
  defaultAvatarUrl = DEFAULT_AVATAR_URL;
  userProfile: UserProfile;
  userAvatar: AvatarInfo;

  @Input()
  inlineMenuActive: boolean

  @Output()
  inlineMenuClick: EventEmitter<null> = new EventEmitter();

  get displayName() {
    const person = this.userProfile?.person;
    if (person?.displayName) {
      return person.displayName;
    } else if (person?.firstName && person.lastName) {
      return person.firstName + ' ' + person.lastName;
    } else {
      return this.authService.currentUser().userId;
    }
  }

  get baseUrl() {
    return this.configService.getPortalData().baseUrl;
  }

  get tabIndex() {
    return !this.inlineMenuActive ? '-1' : null;
  }

  constructor(
    @Inject(AUTH_SERVICE) private authService: IAuthService,
    private router: Router,
    private configService: ConfigurationService
  ) {}

  ngOnInit() {
    this.userProfile = this.authService.currentUser();
    this.authService
      .getAvatar()
      .subscribe(avatar => (this.userAvatar = avatar));
  }

  onClick(event) {
    this.inlineMenuClick.emit(null);
    event.preventDefault();
  }

  logout(event: Event) {
    event.preventDefault();
    this.authService.logout();
  }
}
