import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SupportTicket } from '../../../api/model/support-ticket.model';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'tk-support-ticket',
  templateUrl: './support-ticket.component.html',
  styleUrls: ['./support-ticket.component.scss']
})
export class SupportTicketComponent implements OnInit {
  formGroup: FormGroup;

  @Input()
  display = false;
  @Output()
  displayChange: EventEmitter<boolean> = new EventEmitter();

  @Output()
  submitTicket: EventEmitter<SupportTicket> = new EventEmitter();

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    this.formGroup = this.fb.group({
      title: ['', Validators.required],
      description: ['', Validators.required]
    });
  }

  close(): void {
    this.displayChange.emit(false);
  }

  submit(): void {
    this.submitTicket.emit(this.formGroup.value);
  }
}
