import { Injectable } from "@angular/core";
import { FeedbackDTO } from "../model/feedback.model";
import { HttpClient, HttpParams, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { LaunchpadDTO } from "../model/launchpadDTO";
import { LaunchpadType } from "../model/launchpadType";
import { LaunchpadTileCreateDTO } from "../model/launchpadTileCreateDTO";

export interface GetAllLaunchpadsFilteredRequestParams {
  name?: string;
  portalId?: string;
  type?: LaunchpadType;
  userGroupId?: string;
  userId?: string;
  userOrganizationId?: string;
}

@Injectable({ providedIn: "root" })
export class LaunchpadService {
  url = "./launchpad-api/launchpad";

  constructor(private httpClient: HttpClient) {}

  public getLaunchpads(
    criteria: GetAllLaunchpadsFilteredRequestParams
  ): Observable<LaunchpadDTO[]> {
    const parameters = new HttpParams();
    Object.keys(criteria).forEach(key => parameters.set(key, criteria[key]));
    const options = { params: parameters };
    return this.httpClient.get<LaunchpadDTO[]>(this.url, options);
  }

  public addToLaunchpad(
    id: number,
    createTileDTO: LaunchpadTileCreateDTO
  ): Observable<LaunchpadDTO> {
    const httpOptions = {
      headers: new HttpHeaders({
        "Content-Type": "application/json"
      })
    };

    return this.httpClient.post<LaunchpadDTO>(
      `${this.url}/${id}/tiles`,
      createTileDTO,
      httpOptions
    );
  }
}
