import { Component, OnInit, Input, TemplateRef } from '@angular/core';

@Component({
  selector: 'tk-panel-header',
  template: `
    <div class="panel-header">
      <div class="panel-toolbar-left">
        <ng-container
          *ngTemplateOutlet="leftToolbar ? leftToolbar : defaultLeftToolbar"
        ></ng-container>
      </div>
      <h1 class="panel-heading">{{ header }}</h1>
      <div class="panel-toolbar-right">
        <ng-container
          *ngTemplateOutlet="rightToolbar ? rightToolbar : defaultRightToolbar"
        ></ng-container>
      </div>
    </div>

    <ng-template #defaultLeftToolbar> </ng-template>
    <ng-template #defaultRightToolbar> </ng-template>
  `,
  styles: [
    `
      .panel-header {
        display: flex;
      }
      .panel-heading {
        margin: 0px;
        line-height: 30px;
        font-weight: 100;
      }
      .panel-toolbar-right {
        margin-left: auto;
      }
      .panel-toolbar-left,
      .panel-toolbar-right {
        display: flex;
      }
    `
  ]
})
export class PanelHeaderComponent implements OnInit {
  @Input() header: string;
  @Input() leftToolbar: TemplateRef<any>;
  @Input() rightToolbar: TemplateRef<any>;

  constructor() {}

  ngOnInit() {}
}
