import {
  Directive,
  ElementRef,
  Input,
  Output,
  EventEmitter
} from "@angular/core";

@Directive({
  selector: "[tkCollapsible]"
})
export class CollapsibleDirective {
  constructor(private el: ElementRef) {
    console.log(
      `collapsible directive constructor ${el.nativeElement.classList}`
    );
    el.nativeElement.classList.add("collapsible");
  }

  @Input() isCollapsed = true;

  @Output() collapsed: EventEmitter<any> = new EventEmitter();
  @Output() expanded: EventEmitter<any> = new EventEmitter();

  toggle() {
    this.isCollapsed ? this.show() : this.hide();
  }

  show() {
    this.isCollapsed = false;
  }

  hide() {
    this.isCollapsed = true;
  }
}
