import { Component, OnInit } from '@angular/core';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog/';

@Component({
  template: `
    <div>
      <div class="dialog-content">
        <span
          >No help was found for this item. Please ask your system administrator
          to add it. The context ID for this page is below:</span
        >
        <span class="code p-my-2">{{ pageId }}</span>
      </div>
      <div class="dialog-footer">
        <div class="p-col p-fluid">
          <button
            type="button"
            pButton
            label="Close"
            (click)="close()"
          ></button>
        </div>
      </div>
    </div>
  `,
  styles: [
    `
      .code {
        display: block;
        clear: both;
        padding: 0.5em;
        font-family: monospace;
        background-color: #ececec;
        border-radius: 4px;
      }
    `
  ]
})
export class NoHelpInfoComponent implements OnInit {
  pageId: string;

  constructor(
    public config: DynamicDialogConfig,
    public ref: DynamicDialogRef
  ) {
    this.pageId = config.data.pageItem;
  }

  ngOnInit() {}

  close() {
    this.ref.close();
  }
}
