import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { HelpData } from '../../api/model';


@Component({
  selector: 'tk-help-editor',
  template: `
    <p-dialog
      header="Edit help item for this page"
      [contentStyle]="{ overflow: 'visible' }"
      [style]="{ width: '500px', 'max-height': '400px' }"
      [(visible)]="display"
      (onHide)="close($event)"
    >
      <div class="modal-content">
        <form class="p-grid p-fluid">
          <div class="p-col-12">
            <span class="p-float-label">
              <input
                id="page"
                type="text"
                pInputText
                name="page"
                disabled="true"
                [(ngModel)]="helpItem.helpItemId"
              />
              <label for="page">Page Item</label>
            </span>
          </div>
          <div class="p-col-12">
            <span class="p-float-label">
              <input
                id="baseUrl"
                type="text"
                pInputText
                name="baseUrl"
                [(ngModel)]="helpItem.baseUrl"
              />
              <label for="baseUrl">Base Url</label>
            </span>
          </div>
          <div class="p-col-12">
            <span class="p-float-label">
              <input
                id="relUrl"
                type="text"
                pInputText
                name="relUrl"
                [(ngModel)]="helpItem.resourceUrl"
              />
              <label for="relUrl">Relative Url</label>
            </span>
          </div>
        </form>
      </div>
      <p-footer>
        <button
          pButton
          class="primary"
          value="Save"
          label="Save"
          icon="pi pi-check"
          (click)="save()"
        ></button>
        <button
          pButton
          icon="pi pi-times"
          class="flat"
          value="Cancel"
          label="Cancel"
          (click)="display = false"
        ></button>
      </p-footer>
    </p-dialog>
  `
})
export class HelpEditDialogComponent implements OnInit {
  @Input() display = false;
  @Input() helpItem: HelpData;
  @Output() closed = new EventEmitter<boolean>();

  constructor() {}

  ngOnInit() {}

  public save() {
    this.closed.emit(true);
  }
  public close(event: any) {
    this.closed.emit(false);
  }
}
