import { Component, OnInit } from '@angular/core';
import { HttpProgressLoaderService } from '../http-progress-loader.service';

@Component({
  selector: 'tk-http-progress-loader',
  template: `
    <div class="wrapper" [ngClass]="{ noshow: !loading }">
      <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
        <circle cx="50" cy="50" r="45" />
      </svg>
    </div>
  `,
  styles: [
    `
      :host {
        display: block;
        height: 36px;
      }
      .wrapper {
        position: absolute;
        opacity: 1;
      }
      .wrapper.noshow {
        -webkit-transition: opacity 1s linear;
        -moz-transition: opacity 1s linear;
        -o-transition: opacity 1s linear;
        transition: opacity 1s linear;
        opacity: 0;
      }
      svg {
        animation: 2s linear infinite svg-animation;
        width: 36px;
      }

      .noshow svg {
        animation: none;
      }

      @keyframes svg-animation {
        0% {
          transform: rotateZ(0deg);
        }
        100% {
          transform: rotateZ(360deg);
        }
      }

      circle {
        animation: 1.4s ease-in-out infinite both circle-animation;
        display: block;
        fill: transparent;
        stroke: #ffffff;
        stroke-linecap: round;
        stroke-dasharray: 283;
        stroke-dashoffset: 280;
        stroke-width: 10px;
        transform-origin: 50% 50%;
      }

      @keyframes circle-animation {
        0%,
        25% {
          stroke-dashoffset: 280;
          transform: rotate(0);
        }

        50%,
        75% {
          stroke-dashoffset: 75;
          transform: rotate(45deg);
        }

        100% {
          stroke-dashoffset: 280;
          transform: rotate(360deg);
        }
      }
    `
  ]
})
export class HttpProgressLoaderComponent implements OnInit {
  loading: boolean;
  constructor(private loaderService: HttpProgressLoaderService) {
    this.loaderService.isLoading.subscribe(v => {
      this.loading = v;
    });
  }

  ngOnInit() {}
}
