import {Component, EventEmitter, Input, OnDestroy, OnInit, Output} from '@angular/core';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {MenuItem} from 'primeng/api';
import {MenuService} from '../../../api/services/menu.service'
import {Subscription} from "rxjs";
import {NavigationEnd, Router} from "@angular/router";
import {filter} from "rxjs/operators";

@Component({
  selector: 'tk-menu',
  templateUrl: 'portal-menu.component.html',
})
export class AppMenuComponent {
  @Input()
  menuActive: boolean;
  @Input()
  isStatic: boolean;
  @Input()
  isHorizontal: boolean;
  @Input()
  isMobile: boolean;
  @Input()
  isDesktop: boolean;
  @Input()
  isSlim: boolean;
  @Input()
  menuHoverActive: boolean;
  @Input()
  mobileMenuActive: boolean;

  @Input()
  set portalMenu(items: MenuItem[]) {
    this.menuWithRoot = {
      label: 'Menu',
      icon: 'list',
      items: items
    }
  }

  @Output()
  menuActiveChange: EventEmitter<boolean> = new EventEmitter();
  @Output()
  mobileMenuActiveChange: EventEmitter<boolean> = new EventEmitter();

  menuWithRoot: MenuItem;

  constructor() {}
}

@Component({
  /* eslint-disable @angular-eslint/component-selector */
  selector: '[tk-submenu]',
  /* eslint-enable @angular-eslint/component-selector */
  template: `
    <ng-container>
        <div *ngIf="root">
          <span class="layout-menuitem-text">{{item.label}}</span>
        </div>
        <a [attr.href]="item.url" (click)="itemClick($event)" *ngIf="!item.routerLink || item.items" (keydown.enter)="itemClick($event)"
           [attr.target]="item.target" [attr.tabindex]="0" (mouseenter)="onMouseEnter()" pRipple>
          <i [ngClass]="'pi-' + item.icon" class="layout-menuitem-icon pi pi-fw"></i>
          <span class="layout-menuitem-text">{{item.label}}</span>
          <span class="p-badge p-component p-badge-no-gutter" *ngIf="item.badge && !root">{{item.badge}}</span>
          <i class="pi pi-fw pi-angle-down layout-submenu-toggler" *ngIf="item.items"></i>
        </a>
        <a (click)="itemClick($event)" *ngIf="item.routerLink && !item.items"
           [routerLink]="item.routerLink" routerLinkActive="active-menuitem-routerlink" [routerLinkActiveOptions]="{exact: true}"
           [attr.target]="item.target" [attr.tabindex]="0" (mouseenter)="onMouseEnter()" pRipple>
          <i [ngClass]="'pi-' + item.icon" class="layout-menuitem-icon pi pi-fw"></i>
          <span class="layout-menuitem-text">{{item.label}}</span>
          <span class="p-badge p-component p-badge-no-gutter" *ngIf="item.badge && !root">{{item.badge}}</span>
          <i class="pi pi-fw pi-angle-down layout-submenu-toggler" *ngIf="item.items"></i>
        </a>
        <ul *ngIf="(item.items && root) || (item.items && active)" [@children]="root ? 'visible' : active ? 'visibleAnimated' : 'hiddenAnimated'">
          <ng-template ngFor let-child let-i="index" [ngForOf]="item.items">
            <li tk-submenu [item]="child" [index]="i" [parentKey]="key"></li>
          </ng-template>
        </ul>
    </ng-container>
  `,
  host: {
    '[class.layout-root-menuitem]': 'root || active',
    '[class.active-menuitem]': '(active)'
  },
  animations: [
    trigger('children', [
      state('void', style({
        height: '0px',
        padding: '0px'
      })),
      state('hiddenAnimated', style({
        height: '0px',
        padding: '0px'
      })),
      state('visibleAnimated', style({
        height: '*'
      })),
      state('visible', style({
        height: '*'
      })),
      state('hidden', style({
        height: '0px',
        padding: '0px'
      })),
      transition('visibleAnimated => hiddenAnimated', animate('400ms cubic-bezier(0.86, 0, 0.07, 1)')),
      transition('hiddenAnimated => visibleAnimated', animate('400ms cubic-bezier(0.86, 0, 0.07, 1)')),
      transition('void => visibleAnimated, visibleAnimated => void',
          animate('400ms cubic-bezier(0.86, 0, 0.07, 1)'))
    ])
  ]
})
export class AppSubMenuComponent implements OnInit, OnDestroy {
  @Input() item: MenuItem;

  @Input() root: boolean;

  @Input() visible: boolean;

  @Input() index: number;

  @Input() parentKey: string;

  @Input()
  menuActive: boolean;
  @Input()
  isStatic: boolean;
  @Input()
  isHorizontal: boolean;
  @Input()
  isMobile: boolean;
  @Input()
  isDesktop: boolean;
  @Input()
  isSlim: boolean;
  @Input()
  menuHoverActive: boolean;
  @Input()
  mobileMenuActive: boolean;

  @Output()
  menuActiveChange: EventEmitter<boolean> = new EventEmitter();
  @Output()
  mobileMenuActiveChange: EventEmitter<boolean> = new EventEmitter();

  active = false;

  menuSourceSubscription: Subscription;

  menuResetSubscription: Subscription;

  key: string;

  constructor(private menuService: MenuService, private router: Router) {}

  ngOnInit() {
    this.menuSourceSubscription = this.menuService.menuSource$.subscribe(key => {
      // deactivate current active menu
      if (this.active && this.key !== key && key.indexOf(this.key) !== 0) {
        this.active = false;
      }
    });

    this.menuResetSubscription = this.menuService.resetSource$.subscribe(() => {
      this.active = false;
    })

    // TODO Ultima11: Verify if this logic works with our setup
    this.router.events.pipe(filter(event => event instanceof NavigationEnd))
        .subscribe(params => {
          if (this.isSlim) {
            this.active = false;
          } else {
            if (this.item.routerLink) {
              this.updateActiveStateFromRoute();
            } else {
              this.active = false;
            }
          }
        });

    if ((this.isHorizontal || this.isSlim) && this.item.routerLink) {
      this.updateActiveStateFromRoute()
    }

    this.key = this.parentKey ? this.parentKey + '-' + this.index : String(this.index);
  }

  ngOnDestroy() {
    if (this.menuSourceSubscription) {
      this.menuSourceSubscription.unsubscribe();
    }

    if (this.menuResetSubscription) {
      this.menuResetSubscription.unsubscribe();
    }
  }

  updateActiveStateFromRoute() {
    this.active = this.router.isActive(this.item.routerLink[0], !this.item.items);
  }


  itemClick(event: Event) {
    // avoid processing disabled items
    if (this.item.disabled) {
      event.preventDefault();
      return true;
    }

    this.menuService.onMenuStateChange(this.key)

    // execute command
    if (this.item.command) {
      this.item.command({ originalEvent: event, item: this.item });
    }

    if (this.item.items) {
      this.active = !this.active;
    } else {
      this.active = true;

      if (this.isHorizontal || this.isSlim) {
        this.menuService.reset();
        this.menuHoverActive = false;
      }

      if (!this.isStatic) {
        this.menuActiveChange.emit(false);
      }

      this.mobileMenuActiveChange.emit(false);
    }

    // prevent hash change
    if (this.item.items || (!this.item.url && !this.item.routerLink)) {
      // TODO https://github.com/primefaces/primeng/issues/8751
      // setTimeout(() => {
      //   this.app.layoutMenuScrollerViewChild.moveBar();
      // }, 450);
      event.preventDefault();
    }
  }

  onMouseEnter() {
    if (
      this.root &&
      this.menuHoverActive &&
      (this.isHorizontal || this.isSlim) &&
      this.isDesktop
    ) {
      this.menuService.onMenuStateChange(this.key);
      this.active = true;
    }
  }

}
