import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { stringify } from 'querystring';
import { HelpData } from '../model/help-data.model';
import { Observable } from 'rxjs';
import { map, tap } from 'rxjs/operators';

const baseUrl = './ahm-api/internal/applications';

@Injectable({ providedIn: 'root' })
export class HelpPageAPIService {
  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };
  constructor(private httpClient: HttpClient) {}

  getHelpDataItem(
    appId: string,
    helpItemId: string,
    type = 'PAGE'
  ): Observable<HelpData> {
    return this.httpClient
      .get<HelpData[]>(`${baseUrl}/${appId}/helpItems`, {
        params: {
          helpItemId
        }
      })
      .pipe(map(helpItems => helpItems[0]));
  }

  getHelpDataItemLegacy(
    appId: string,
    context: string,
    type = 'PAGE'
  ): Observable<HelpData> {
    console.log(
      `About to go to ${baseUrl}/helpdata/${appId}/${encodeURIComponent(
        context
      )}`
    );
    return this.httpClient.post<HelpData>(
      `${baseUrl}/helpdata/${appId}/${encodeURIComponent(context)}`,
      `"${type}"`,
      {
        headers: {
          'Content-Type': 'application/v1+json',
          Accept: 'application/v1+json'
        }
      }
    );
  }

  saveHelpPage(
    appId: string,
    helpItem: HelpData
  ): Observable<HttpResponse<any>> {
    if (helpItem.id) {
      return this.httpClient.patch(
        `${baseUrl}/${appId}/helpItems/${helpItem.id}`,
        helpItem,
        {
          observe: 'response'
        }
      );
    } else {
      return this.httpClient.post(`${baseUrl}/${appId}/helpItems`, helpItem, {
        observe: 'response'
      });
    }
  }

  saveHelpPageLegacy(helpItem: HelpData): Observable<HttpResponse<any>> {
    return this.httpClient.post(`${baseUrl}/helpdata`, helpItem, {
      headers: {
        'Content-Type': 'application/v1+json',
        Accept: 'application/v1+json'
      },
      observe: 'response'
    });
  }
}
