import {
  Directive,
  Input,
  ElementRef,
  NgZone, 
  AfterViewInit
} from '@angular/core';
import { Tooltip } from 'primeng/tooltip';

@Directive({ selector: '[pTooltip-aria]' })
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export class TooltipAriaDirective extends Tooltip implements AfterViewInit {
  constructor(public el: ElementRef, public zone: NgZone) {
    super(el, zone);
  }
  
  public ngAfterViewInit(): void {
    this.zone.runOutsideAngular(() => {
      switch (this.tooltipEvent) {
        case 'hover':
          this.initHoverForTooltipEvent();
          break;
        case 'focus':
          this.initFocusForTooltipEvent();
          break;
        case 'hover|focus':
          this.initHoverForTooltipEvent();
          this.initFocusForTooltipEvent();
          break;
        case 'focus|hover':
          this.initHoverForTooltipEvent();
          this.initFocusForTooltipEvent();
          break;
      }
    });
  }
  
  private initHoverForTooltipEvent(): void {
    this.mouseEnterListener = this.onMouseEnter.bind(this);
    this.mouseLeaveListener = this.onMouseLeave.bind(this);
    this.clickListener = this.onClick.bind(this);
    this.el.nativeElement.addEventListener('mouseenter', this.mouseEnterListener);
    this.el.nativeElement.addEventListener('mouseleave', this.mouseLeaveListener);
    this.el.nativeElement.addEventListener('click', this.clickListener);
  }
  
  private initFocusForTooltipEvent(): void {
    this.focusListener = this.onFocus.bind(this);
    this.blurListener = this.onBlur.bind(this);
    this.el.nativeElement.addEventListener('focus', this.focusListener);
    this.el.nativeElement.addEventListener('blur', this.blurListener);
  }
  
  @Input('pTooltip-aria') set pTooltipText(text: string) {
    this.text = text;
  }
}
