import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SearchItemDTO } from '../model/search-item.model';

@Injectable({ providedIn: 'root' })
export class PortalItemService {
  private readonly url = 'portal-api/v1/portalItems/search';
  constructor(private readonly http: HttpClient) {}

  searchForServices(query: string, portalId: string, count?: number): Observable<SearchItemDTO[]> {
    let params = new HttpParams();
    if (query) {
      params = params.set('query', query);
    }
    if (portalId) {
      params = params.set('portal-id', portalId);
    }
    if (count) {
      params = params.set('count', count.toString());
    }
    return this.http.get<SearchItemDTO[]>(this.url, {
      params
    });
  }
}
