import { Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output, ViewChild } from '@angular/core'
import { Observable } from 'rxjs'
import { IAuthService } from '../../../api/iauth.service'
import { AUTH_SERVICE } from '../../../api/injection-tokens'
import { AvatarInfo } from '../../../api/model/avatar-info.model'
import { UserProfile } from '../../../api/model/user-profile.model'
import { CONFIG_KEY_TKIT_SEARCH_BASE_URL, DEFAULT_AVATAR_URL } from '../../constants'
import { ConfigurationService } from '../../../api/configuration.service'
import { StateService } from '../../../api/state.service'
import { Router } from '@angular/router'
import { animate, AnimationEvent, style, transition, trigger } from '@angular/animations'

@Component({
  selector: 'tk-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  animations: [
    trigger('topbarActionPanelAnimation', [
      transition(':enter', [
        style({ opacity: 0, transform: 'scaleY(0.8)' }),
        animate('.12s cubic-bezier(0, 0, 0.2, 1)', style({ opacity: 1, transform: '*' })),
      ]),
      transition(':leave', [animate('.1s linear', style({ opacity: 0 }))]),
    ]),
  ],
})
export class HeaderComponent implements OnInit {
  defaultAvatarUrl = DEFAULT_AVATAR_URL
  menuExpanded: boolean
  menuItems: any
  userProfile: UserProfile
  userDisplayName: string
  userAvatar: Observable<AvatarInfo>
  searchUrl: string

  @ViewChild('searchInput') searchInputViewChild: ElementRef

  mobileTopbarActive: boolean

  // TODO doesn't always work (cd related?)
  @Input()
  menuButtonTitle: string
  @Output()
  menuButtonClick: EventEmitter<any> = new EventEmitter()
  @Output()
  topbarItemClick: EventEmitter<any> = new EventEmitter()
  @Output()
  openHelpPage: EventEmitter<any> = new EventEmitter()
  @Output()
  openHelpPageEditor: EventEmitter<any> = new EventEmitter()
  @Output()
  openFeedback: EventEmitter<any> = new EventEmitter()
  @Output()
  openAddToLaunchpad: EventEmitter<any> = new EventEmitter()
  @Output()
  openSupportTicket: EventEmitter<any> = new EventEmitter()
  @Output()
  searchClick: EventEmitter<any> = new EventEmitter()
  @Input()
  profileMode: string
  @Input()
  isHorizontal: boolean
  @Input()
  activeTopbarItem: any
  @Input()
  disableBreadcrumbs: boolean
  @Input()
  fullPortalLayout: boolean
  @Input()
  search: boolean

  @Input()
  homeNavUrl = '/'
  @Input()
  homeNavTitle = 'Home'

  constructor(
    @Inject(AUTH_SERVICE) private authService: IAuthService,
    private config: ConfigurationService,
    private stateService: StateService,
    private router: Router
  ) {}

  ngOnInit() {
    this.userProfile = this.authService.currentUser()
    this.userAvatar = this.authService.getAvatar()
    this.userDisplayName = this.authService.currentUser().person.displayName
    this.searchUrl = this.config.getProperty(CONFIG_KEY_TKIT_SEARCH_BASE_URL) || '/ops/enterprise-search'
  }

  clickMenu() {
    console.log('Menu click')
    this.menuExpanded = !this.menuExpanded
  }

  navigateTo(path: string, event: Event) {
    event.preventDefault()
    path = this.config.getPortalData().baseUrl + path
    location.assign(path)
  }

  logout(event: Event) {
    event.preventDefault()
    this.authService.logout()
  }

  onMenuButtonClick(e: Event) {
    this.menuButtonClick.emit(e)
  }
  onTopbarItemClick(e: Event, arg: any) {
    this.topbarItemClick.emit({ event: e, arg })
  }
  onOpenHelpPageEditor(e: Event) {
    this.openHelpPageEditor.emit(e)
  }
  onOpenHelpPage(e: Event) {
    this.openHelpPage.emit(e)
  }
  onOpenFeedback(e: Event) {
    this.openFeedback.emit(e)
  }
  onAddToFavourites(e: Event) {
    this.openAddToLaunchpad.emit(e)
  }
  onOpenSupportTicket(e: Event) {
    this.openSupportTicket.emit(e)
  }

  onTopbarMobileButtonClick($event: MouseEvent) {
    this.mobileTopbarActive = !this.mobileTopbarActive
    $event.preventDefault()
  }

  onSearchClick(e: Event, val: boolean) {
    this.searchClick.emit({ event: e, val })
  }

}
